.Preview {
  margin: auto;
  margin-top: 10px;
  width: 80vw;
  height: fit-content;
  padding: 10px;
  display: flex;
  flex-direction: column; }

.Preview .preview-header {
  width: 98%;
  margin: 10px;
  margin-bottom: 0;
  border: 1px solid black;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
  background-color: #4aa3a3; }

.Preview .preview-header h3 {
  margin: 5px;
  float: left; }

.Preview .preview-header #expand {
  margin: 6px;
  float: right; }

.Preview #preview {
  border: 1px solid black;
  border-top: none;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
  background-color: #c0d8d8;
  width: 97.08%;
  resize: vertical;
  height: 300px;
  margin: 10px;
  margin-top: 0;
  padding: 5px;
  outline: none; }
