.Editor {
  margin: auto;
  margin-top: 10px;
  width: 700px;
  height: fit-content;
  padding: 10px;
  display: flex;
  flex-direction: column; }

.Editor .editor-header {
  width: 90%;
  margin: 10px;
  margin-bottom: 0;
  border: 1px solid black;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
  background-color: #4aa3a3; }

.Editor .editor-header h3 {
  margin: 5px;
  float: left; }

.Editor .editor-header #expand {
  margin: 6px;
  float: right; }

.Editor #editor {
  border: 1px solid black;
  border-top: none;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
  background-color: #c0d8d8;
  width: 88.7%;
  height: 200px;
  margin: 10px;
  margin-top: 0;
  padding: 5px;
  outline: none; }
